const Screens = {};


    Screens['user-management'] = {"id":"user-management","title":"User Management","icon":"group","model":"SystemSettings","view":"UserManagement","access":"read","group_id":"system-settings","extension":"Lanes","description":"Add/Remove/Modify User Accounts","asset":"lanes/screens/user-management"};
